.Profile{
    width: 40%;
    background-color: #ffffff;
    margin: auto;
    border: 2px solid #d8e0ea;
    border-radius: 1rem;
    
    margin-bottom: 2rem;
}

.description-container{
    text-align: center;
}

.avatar{

    display: block;
    margin: auto;
    border: 0;
    border-radius: 50%;
    margin-top:1rem;

}
